'use strict';

function toggleForm(buttonNumber) {
  const form = document.getElementById(`signup-form-${buttonNumber}`);
  const signupButton = document.querySelector(
    `#show-form-button-${buttonNumber}`
  );

  const formDisplayStyle = window
    .getComputedStyle(form)
    .getPropertyValue('display');

  if (formDisplayStyle === 'none') {
    form.style.display = 'block';
    signupButton.style.display = 'none';
  } else {
    form.style.display = 'none';
    signupButton.style.display = 'block';
  }
}

document.addEventListener('DOMContentLoaded', function() {
  const signupButtons = document.querySelectorAll('.signup-button');

  signupButtons.forEach(function(button, index) {
    button.addEventListener('click', function() {
      toggleForm(index + 1);
    });
  });
});
